import vtkPolyData from "@kitware/vtk.js/Common/DataModel/PolyData";
import vtkCellArray from "@kitware/vtk.js/Common/Core/CellArray";
import vtkMapper from "@kitware/vtk.js/Rendering/Core/Mapper";
import vtkActor from "@kitware/vtk.js/Rendering/Core/Actor";

export function ECGFP(polyData) {

    const polys = []

    // Fonction pour effectuer une recherche en profondeur (DFS)  partir d'une cellule donne
    function depthFirstSearch(cellId, visited, group) {
        // console.log('DPS', cellId, visited, group)
        visited[cellId] = true;
        group.push(cellId);

        // const cell = polyData.getCell(cellId);
        const cellPoints = polyData.getCellPoints(cellId).cellPointIds;
        console.log(`CELL POINTS FOR ${cellId} : `, cellPoints)

        const neighborIds = [];
        // console.log('CELLPOINTS', cellPoints, cellPoints.length)

        // Parcours des points de la cellule pour trouver les cellules voisines
        for (let i = 0; i < cellPoints.length; i++) {
            const pointId = cellPoints[i];
            const neighborCellIds = polyData.getPointCells(pointId);
            console.log(`NEIGHBOUR CELLS FOR ${pointId} : `, neighborCellIds)

            // Ajout des cellules voisines non visites  la liste
            for (let j = 0; j < neighborCellIds.length; j++) {
                const neighborCellId = neighborCellIds[j];
                if (!visited[neighborCellId]) {
                    neighborIds.push(neighborCellId);
                }
            }
        }

        // Rcursivement, effectue une recherche en profondeur pour chaque cellule voisine non visite
        for (let i = 0; i < neighborIds.length; i++) {
            const neighborId = neighborIds[i];
            if (!visited[neighborId]) {
                depthFirstSearch(neighborId, visited, group);
            }
        }
    }

    // Obtention du nombre total de cellules du vtkPolyData initial
    const totalNumberOfCells = polyData.getNumberOfCells();
    // console.log('NB OF CELLS', totalNumberOfCells)

    // Tableau pour suivre les cellules visites
    const visited = new Array(totalNumberOfCells).fill(false);

    // Tableau pour stocker les groupes de cellules connectes
    const cellGroups = [];

    // Parcours de toutes les cellules du vtkPolyData initial
    for (let cellId = 0; cellId < totalNumberOfCells; cellId++) {
        if (!visited[cellId]) {
            // Dbut d'un nouveau groupe de cellules connectes
            const group = [];
            depthFirstSearch(cellId, visited, group);

            // Ajout du groupe de cellules connectes  la liste
            console.log('GROUP', group)
            cellGroups.push(group);
        }
    }

    // Cration du mapper et de l'acteur pour chaque groupe de cellules connectes
    for (let groupId = 0; groupId < cellGroups.length; groupId++) {
        const groupCellIds = cellGroups[groupId];
        // console.log('GCIDs', groupCellIds)

        const groupPolyData = vtkPolyData.newInstance();
        const groupCellArray = vtkCellArray.newInstance();

        // Copie des cellules du groupe dans le vtkPolyData du groupe
        for (let i = 0; i < groupCellIds.length; i++) {
            const cellId = groupCellIds[i];
            const cellPointids = polyData.getCellPoints(cellId).cellPointIds;
            groupCellArray.insertNextCell(cellPointids);
        }

        // Copie des points du vtkPolyData initial dans le vtkPolyData du groupe
        groupPolyData.shallowCopy(polyData);
        groupPolyData.setPolys(groupCellArray);

        polys.push(groupPolyData)
    }

    return polys
}

export function ECGFP_iterative(polyData) {
    const polys = [];
    const totalNumberOfCells = polyData.getNumberOfCells();
    const visited = new Array(totalNumberOfCells).fill(false);
    const cellGroups = [];

    function getNeighborCellIds(pointId) {
        return polyData.getPointCells(pointId);
    }

    // Traverse cells and form connected cell groups
    for (let cellId = 0; cellId < totalNumberOfCells; cellId++) {
        if (!visited[cellId]) {
            const group = [];
            const neighborIds = [cellId];

            // Depth-first search to find connected cell groups
            while (neighborIds.length > 0) {
                const currentCellId = neighborIds.pop();

                if (!visited[currentCellId]) {
                    visited[currentCellId] = true;
                    group.push(currentCellId);

                    // Find neighboring cells for each point in the cell
                    polyData.getCellPoints(currentCellId).cellPointIds.forEach((pointId) => {
                        getNeighborCellIds(pointId).forEach((neighborCellId) => {
                            if (!visited[neighborCellId]) {
                                neighborIds.push(neighborCellId);
                            }
                        });
                    });
                }
            }

            cellGroups.push(group);
        }
    }
    console.log('CELL GROUPS', cellGroups, cellGroups.length);

    // Create polyData for each connected cell group
    cellGroups.forEach((groupCellIds) => {
        const groupPolyData = vtkPolyData.newInstance();
        const groupCellArray = vtkCellArray.newInstance();

        // Copy cells of the group to new vtkPolyData
        groupCellIds.forEach((cellId) => {
            const cellPointids = polyData.getCellPoints(cellId).cellPointIds;
            groupCellArray.insertNextCell(cellPointids);
        });

        // Copy points from initial vtkPolyData to the group vtkPolyData
        groupPolyData.shallowCopy(polyData);
        groupPolyData.setPolys(groupCellArray);
        polys.push(groupPolyData);
    });

    return polys;
}
