import React, {useEffect, useState} from 'react';
import {DicomMetadataStore} from '@ohif/core';
import {Button, useImageViewer, useViewportGrid} from '@ohif/ui'
import {calculateNormalCurve, cubicSplineInterpolation, sharpen} from './computePanoramicUtils.js'
import {ToolGroupManager, utilities as csToolsUtils} from '@cornerstonejs/tools';
import FileLoaderService from '../../../../platform/app/src/routes/Local/fileLoaderService.js'
import {dcmbuffer_to_png} from './dcmbuffer_to_png.js'
import {LSF_methods} from './computeDentalCurveUtils.js'
import {Enums, metaData, cache} from '@cornerstonejs/core';
import ProgressBar from "@ramonak/react-progress-bar";
import {number} from "prop-types";

const skmeans = require('skmeans')

interface BASE_POINT {
    point_A: number[];
    point_B: number[];
    vector_director: number[];
    compute_long_axial_curve: boolean;
}

//TODO: These should be a function parameter

const NUMBER_CONTROL_POINTS = 7;
const NUMBER_BASE_POINTS = 11;
const THICKNESS = 70;
const DENTASCAN_THRESHOLD_VALUE = 300
const NORMAL_LONG_AXIAL_CURVE_THICKNESS = 30;

function delay(time){
    return new Promise(resolve => setTimeout(resolve, time));
}

export default function PanoramicGenerationPanel({
                                           commandsManager,
                                           extensionManager,
                                           servicesManager
                                       }): React.FunctionComponent {
    // @ts-ignore
    const { StudyInstanceUIDs } = useImageViewer();
    // @ts-ignore
    const [{ activeViewportIndex, viewports }, viewportGridService] = useViewportGrid();
    const {
        displaySetService,
        cornerstoneViewportService,
        hangingProtocolService,
        toolGroupService,
        measurementService,
        uiNotificationService,
        cornerstoneCacheService,
        customizationService
    } = servicesManager.services;

    const {
        displaySetInstanceUIDs,
        displaySetOptions,
        viewportOptions,
    } = viewports[activeViewportIndex];

    const [panoramicMetadada, setPanoramicMetadada] = useState({
        update: false,
        dentascanViewportData: undefined,
        panoramicViewportData: undefined,
        panoramic: {
            width: 0,
            height: 0,
        }
    })

    //Progress bar for panoramic computation
    const [isProcessingPanoramic, setIsProcessingPanoramic] = useState(false)
    const [processingPourcent, setProcessingPourcent] = useState(0)

    //Panoramic computation settings
    const [numberBasePoints, setNumberBasePoints] = useState(11)

    useEffect(() => {
        const viewport = cornerstoneViewportService.getCornerstoneViewportByIndex(0)
        const general_series_module = metaData.get('generalSeriesModule', viewport.getCurrentImageId())

        const filterMap = (map, predicate) => {
            const fmap = new Map()
            map.forEach((value, key) => {
                if(predicate(value, key)) fmap.set(key, value)
            })
            return fmap
        }

        const filterDSPanoramic = () => {
            return filterMap(displaySetService.getDisplaySetCache(), (value, key) => {
                return value.SeriesInstanceUID === `${general_series_module.seriesInstanceUID}.panoramic`
            })
        }
        const filterDSContrasted = () => {
            return filterMap(displaySetService.getDisplaySetCache(), (value, key) => {
                return value.SeriesInstanceUID === `${general_series_module.seriesInstanceUID}.contrasted`
            })
        }
        function delay(time){
            return new Promise(resolve => setTimeout(resolve, time));
        }

        delay(300).then(() => {
            //Check if panoramic has already been created and matches to the series uid. If so, set hanging protocol index to 1
            const filteredDSPanoramic = filterDSPanoramic()
            const filteredDSContrasted = filterDSContrasted()
            // console.log('FDS', filteredDSPanoramic, displaySetService.getDisplaySetCache())
            if(filteredDSPanoramic.size >= 1){
                commandsManager.runCommand('toggleHangingProtocol', {
                    protocolId: 'panoramicViewport',
                    stageIndex: 2
                })
                const previousPanoramicMetadata = cornerstoneCacheService.getPanoramicStateCache(general_series_module.seriesInstanceUID)
                setPanoramicMetadada({...previousPanoramicMetadata, update: !previousPanoramicMetadata.update})
            }
            else if(filteredDSContrasted.size >= 1){
                commandsManager.runCommand('toggleHangingProtocol', {
                    protocolId: 'panoramicViewport',
                    stageIndex: 1
                })
            }
            else
            {
                commandsManager.runCommand('toggleHangingProtocol', {
                    protocolId: 'panoramicViewport',
                    stageIndex: 0
                })
            }
        })
    }, [])

    useEffect(() => {
        //If panoramic is updating, processing is logically done, so we can turn off progress bar
        setIsProcessingPanoramic(false)

        //============================= DELETE ALL PREVIOUS DENTASCAN ANNOTATIONS =================//
        const dentascanMeasurements = measurementService.getMeasurements()
            .filter(m => {return m.toolName === "ArrowAnnotate"})

        // Delete measurements that are not handmade (thus the interpolated line)
        dentascanMeasurements.forEach(pm => {
            if(pm.metadata.dentascan){
                commandsManager.runCommand('deleteMeasurement', {
                    uid: pm.uid
                })
            }
        })

        //================== DISPLAY DENTASCAN SECTION IN PANORAMIC AS ANNOTATION ===========================//
        if(!panoramicMetadada.dentascanViewportData) return;

        const viewport = cornerstoneViewportService.getCornerstoneViewportByIndex(1)
        if(!viewport){
            return
        }

        const imageData = viewport.getImageData();

        const pointUp = imageData.imageData.indexToWorld([panoramicMetadada.dentascanViewportData?.data.initialImageIndex, 0, 0])
        const pointDown = imageData.imageData.indexToWorld([panoramicMetadada.dentascanViewportData?.data.initialImageIndex, panoramicMetadada.panoramic.height, 0])

        //Get ArrowAnnotate instance in order to create new annotation indicating dentascan section on panoramic
        const toolGroup = ToolGroupManager.getToolGroupForViewport(
            viewport.id,
            viewport.renderingEngineId
        );
        const arrowAnnotateInstance = toolGroup.getToolInstance("ArrowAnnotate")
        arrowAnnotateInstance.addNewDentascanAnnotation(viewport.element, [pointUp, pointDown], 'rgb(255, 255, 255)')


        //================== DISPLAY WANTED DENTASCANS IN BOTTOM VIEWPORT ===========================//

        csToolsUtils.jumpToSlice(cornerstoneViewportService.getCornerstoneViewportByIndex(2).element, {
            imageIndex: panoramicMetadada.dentascanViewportData.data.initialImageIndex - 4,
            debounceLoading: true,
        });
        csToolsUtils.jumpToSlice(cornerstoneViewportService.getCornerstoneViewportByIndex(3).element, {
            imageIndex: panoramicMetadada.dentascanViewportData.data.initialImageIndex - 2,
            debounceLoading: true,
        });
        csToolsUtils.jumpToSlice(cornerstoneViewportService.getCornerstoneViewportByIndex(4).element, {
            imageIndex: panoramicMetadada.dentascanViewportData.data.initialImageIndex,
            debounceLoading: true,
        });
        csToolsUtils.jumpToSlice(cornerstoneViewportService.getCornerstoneViewportByIndex(5).element, {
            imageIndex: panoramicMetadada.dentascanViewportData.data.initialImageIndex + 2,
            debounceLoading: true,
        });
        csToolsUtils.jumpToSlice(cornerstoneViewportService.getCornerstoneViewportByIndex(6).element, {
            imageIndex: panoramicMetadada.dentascanViewportData.data.initialImageIndex + 4,
            debounceLoading: true,
        });

        //Save state in cache to be retrieved if we reopen panoramic panel
        const general_series_module = metaData.get('generalSeriesModule', viewport.getCurrentImageId())
        if(panoramicMetadada.panoramic.width !== 0) cornerstoneCacheService.setPanoramicStateCache(panoramicMetadada, general_series_module.seriesInstanceUID)

    }, [panoramicMetadada.update])

    const loadMultipleBlobsInViewport = (metadata) => {
        const options = {
            prefilledValues:{
                SeriesInstanceUID: metadata.SeriesInstanceUID,
                SOPInstanceUID: Math.random()
                    .toString(36)
                    .substring(2, 10),
                StudyInstanceUID: metadata.StudyInstanceUID,
                FrameOfReferenceUID: Math.random()
                    .toString(36)
                    .substring(2, 10)
            }
        }
        const pixel_data = metadata.PixelData

        return new Promise((resolve, reject) => {
            const create_dataset_promise = new Promise((resolve, reject) => {
                let datasets_stack = []
                for (let x = 0; x < pixel_data.length; x++) datasets_stack.push(null)
                pixel_data.forEach((layer, layer_i) => {
                    metadata.PixelData = layer
                    //Height is always the same, but width can change
                    if(metadata.Columns !== (layer.length / metadata.Rows)) metadata.Columns = layer.length / metadata.Rows
                    const png_buf = dcmbuffer_to_png(metadata, true)
                    png_buf.then((obj) => {
                        const colored_buffer = new Uint8ClampedArray(obj.width * obj.height * 4)
                        const buffer = obj.buffer.data
                        let i = 0
                        buffer.forEach(p => {
                            colored_buffer[i] = p
                            colored_buffer[i + 1] = p
                            colored_buffer[i + 2] = p
                            colored_buffer[i + 3] = 255
                            i += 4
                        })

                        // console.log('OBJ', obj, colored_buffer, colored_buffer.length)
                        const canvas = document.createElement('canvas');
                        canvas.width = obj.width;
                        canvas.height = obj.height;
                        const imgData = new ImageData(new Uint8ClampedArray(colored_buffer), obj.width, obj.height);
                        canvas.getContext('2d').putImageData(imgData, 0, 0);
                        canvas.toBlob((blob) => {
                            const fileLoaderService = new FileLoaderService(blob)
                            const imageId = fileLoaderService.addFile(blob)
                            fileLoaderService.loadFile(blob, imageId).then(img => {
                                const layer_SopInstanceUID = `${options.prefilledValues.SOPInstanceUID}.${layer_i + 1}`
                                fileLoaderService.getDataset(img, imageId, {
                                    prefilledValues: {
                                        ...options.prefilledValues,
                                        SOPInstanceUID: layer_SopInstanceUID,
                                        FrameNumber: layer_i
                                    }
                                }).then((dataset) => {
                                    DicomMetadataStore.addInstance(dataset)
                                    datasets_stack[layer_i] = dataset

                                    if(datasets_stack.length === pixel_data.length &&
                                        !datasets_stack.includes(null))
                                        resolve(datasets_stack)
                                })
                            });
                        })


                    })
                })
            })

            create_dataset_promise.then((ds_stack : any[]) => {
                const display_set_added = displaySetService.makeDisplaySets(ds_stack)
                const dataSource = extensionManager.getDataSource()[0]

                cornerstoneCacheService.createViewportData(
                    display_set_added,
                    {
                        toolGroupId: 'mpr',
                        viewportType: 'stack',
                        initialImageOptions: {
                            preset: 'middle',
                        },
                    },
                    dataSource,
                    ~~(ds_stack.length / 2)
                )
                    .then((viewportData) => {
                        resolve(viewportData)
                    })
            })
        })
    }

    const loadBlobInViewport = (metadata) => {
        return new Promise((resolve, reject) => {
            const png_buf = dcmbuffer_to_png(metadata)
            png_buf.then(obj => {
                const colored_buffer = new Uint8ClampedArray(obj.width * obj.height * 4)
                const buffer = obj.buffer.data
                let i = 0
                buffer.forEach(p => {
                    colored_buffer[i] = p
                    colored_buffer[i + 1] = p
                    colored_buffer[i + 2] = p
                    colored_buffer[i + 3] = 255
                    i += 4
                })

                // console.log('OBJ', obj, colored_buffer, colored_buffer.length)
                const canvas = document.createElement('canvas');
                canvas.width = obj.width;
                canvas.height = obj.height;
                const imgData = new ImageData(new Uint8ClampedArray(colored_buffer), obj.width, obj.height);
                canvas.getContext('2d').putImageData(imgData, 0, 0);

                canvas.toBlob((blob) => {
                    const fileLoaderService = new FileLoaderService(blob)
                    const imageId = fileLoaderService.addFile(blob)
                    fileLoaderService.loadFile(blob, imageId).then(img => {
                        fileLoaderService.getDataset(img, imageId, {
                            prefilledValues: {
                                SeriesInstanceUID: metadata.SeriesInstanceUID,
                                StudyInstanceUID: metadata.StudyInstanceUID
                            }
                        }).then((dataset) => {
                            console.log('DATASET', dataset)
                            DicomMetadataStore.addInstance(dataset)
                            const display_set_added = displaySetService.makeDisplaySets([dataset])
                            console.log('DSADDED', display_set_added)
                            const dataSource = extensionManager.getDataSource()[0]

                            cornerstoneCacheService.createViewportData(
                                display_set_added,
                                {
                                    toolGroupId: 'mpr',
                                    viewportType: 'stack',
                                    initialImageOptions: {
                                        preset: 'middle',
                                    },
                                },
                                dataSource,
                                undefined
                            ).then((viewportData) => {
                                console.log('VIEWPORT STACK DATA', viewportData)
                                hangingProtocolService.refreshStudyList()
                                resolve(viewportData)
                            })
                        })
                    });
                })
            })
        })
    }

    const drawPanoramicLine = () => {
        commandsManager.runCommand('setToolActive', {
            toolName: 'ArrowAnnotate'
        })
    }

    //Debug function to display image in canvas below application main canvas (cancel override-hidden-y)
    const debugShowImage = (pixel_array, rows, columns) => {
        return new Promise<HTMLCanvasElement>((resolve, reject) => {
            //Get volume imageId
            const indexZeroDisplaySet = displaySetService.getDisplaySetByUID(
                cornerstoneViewportService.getViewportInfoByIndex(0).viewportData.data.displaySetInstanceUID
            )
            let imageId;
            //If image is contrasted
            if(indexZeroDisplaySet?.SeriesInstanceUID.includes('contrasted')){
                const baseVolumeSeriesUID = indexZeroDisplaySet.SeriesInstanceUID.replace('.contrasted', '')
                const baseVolumeSeries = DicomMetadataStore.getSeries(indexZeroDisplaySet.StudyInstanceUID, baseVolumeSeriesUID)
                imageId = baseVolumeSeries.instances[0].imageId
            }
            //Else image at index 0 is image we want to get infos from
            else{
                imageId = cornerstoneViewportService.getCornerstoneViewportByIndex(0).getCurrentImageId()
            }

            const voi_lut_module = metaData.get('voiLutModule', imageId)
            const image_pixel_module = metaData.get('imagePixelModule', imageId)
            const metadata = {
                BitsAllocated: image_pixel_module.bitsAllocated,
                BitsStored: image_pixel_module.bitsStored,
                Rows: rows,
                Columns: columns,
                WindowCenter: voi_lut_module.windowCenter[0],
                WindowWidth: voi_lut_module.windowWidth[0],
                PixelData: pixel_array
            }
            const png_bug = dcmbuffer_to_png(metadata)
            png_bug.then(obj => {
                const colored_buffer = new Uint8ClampedArray(obj.width * obj.height * 4)
                const buffer = obj.buffer.data
                let i = 0;
                buffer.forEach(p => {
                    colored_buffer[i] = p
                    colored_buffer[i + 1] = p
                    colored_buffer[i + 2] = p
                    colored_buffer[i + 3] = 255
                    i += 4
                })
                const canvas = document.createElement('canvas')
                canvas.width = obj.width
                canvas.height = obj.height
                const imgData = new ImageData(new Uint8ClampedArray(colored_buffer), obj.width, obj.height)
                canvas.getContext('2d').putImageData(imgData, 0, 0)
                document.body.appendChild(canvas)

                console.info('[DEBUG] - Provided array displayed in document below main canvas !')
                resolve(canvas)
            })
        })
    }

    //Debug function to display random selected points in bone cluster
    const drawPoint = (context, x, y, label, color, size) => {
        if (color == null) {
            color = '#000';
        }
        if (size == null) {
            size = 5;
        }
        var radius = 0.5 * size;
        // to increase smoothing for numbers with decimal part
        var pointX = Math.round(x - radius);
        var pointY = Math.round(y - radius);
        context.beginPath();
        context.fillStyle = color;
        context.fillRect(pointX, pointY, size, size);
        context.fill();
        if (label) {
            var textX = Math.round(x);
            var textY = Math.round(pointY - 5);
            context.font = 'Italic 14px Arial';
            context.fillStyle = color;
            context.textAlign = 'center';
            context.fillText(label, textX, textY);
        }
    }

    //____________________________ FOR PANORAMIC COMPUTATION _________________________//

    //Get normal equation at abscissa i for function lsf (degree 2 polynomial)
    const equationOfNormalPoly2 = (i, lsf) => {return (x) => {
        const derivativeAtI = 2 * lsf.equation[0] * i + lsf.equation[1];
        const slopeOfNormal = -1 / derivativeAtI;
        return slopeOfNormal * (x - i) + (lsf.equation[0] * i * i + lsf.equation[1] * i + lsf.equation[2])
    }}

    function equationOfNormal(i, f){
        const h = 1e-5; // Petit dcalage pour calculer la drive
        const df = (f(i + h) - f(i - h)) / (2 * h);
        const slopeOfNormal = -1 / df
        return x => {
            return slopeOfNormal * (x - i) + f(i)
        }
    }

    //Calculate the angle of rotation of the normal curve with respect to the x-axis
    const calculateRotationAngle = (A, B) => {
        const vectorX = [1, 0];
        const vectorAB = [B[0] - A[0], B[1] - A[1]];

        const angleRadians = Math.atan2(vectorAB[1], vectorAB[0]) - Math.atan2(vectorX[1], vectorX[0]);
        let angleDegrees = (angleRadians * 180) / Math.PI;

        angleDegrees = (angleDegrees + 360) % 360;

        return angleDegrees;
    }

    //Change the direction vector of the Cartesian axis [0, 1] and [1, 0] to have an axis parallel to the normal curve.
    const rotateVector = (vector, alpha) => {
        const radians = (Math.PI / 180) * alpha;
        const x = vector[0] * Math.cos(radians) - vector[1] * Math.sin(radians);
        const y = vector[0] * Math.sin(radians) + vector[1] * Math.cos(radians);
        return [x, y];
    }

    //Find the point on the function f such that the distance with p is d.
    //Useful to find point of thickness desired for dentascan.
    function findPointAtDistance(f, d, p) {
        const p2 = [p[0] + d * 2, f(p[0] + d * 2)]
        const m = (p2[1] - p[1]) / (p2[0] - p[0])
        const alpha = Math.atan(m)
        const x_vect = d * Math.cos(alpha)
        const x = p[0] + x_vect
        return [x, f(x)]
    }

    //Width becomes height, and height becomes width
    const transposeImage = (arr, width, height) => {
        const transposedArr = [];
        for (let i = 0; i < width; i++) {
            for (let j = 0; j < height; j++) {
                transposedArr.push(arr[j * width + i]);
            }
        }
        return transposedArr;
    }

    //Get x and y coordinated from flatten image of dimensions width * height
    const getCoordinatesFromIndex = (i, w, h) => ({ x: i % w, y: Math.floor(i / w) });

    //Find minX and maxX where values are different than -1000 on a width * height image
    const findXRange = (image, width, height) => {
        let minX = Infinity;
        let maxX = -Infinity;

        for (let i = 0; i < width * height; i++) {
            const x = i % width;
            const pixelValue = image[i];

            if (pixelValue !== -1000) {
                minX = Math.min(minX, x);
                maxX = Math.max(maxX, x);
            }
        }

        return { minX, maxX };
    }

    //Get dentascan section at abscissa i for a degree 2 polynomial fitting curve lsf.
    //Length of normal is dentascan_thickness
    //Dicom of the dentascan desired is defined by its dicom_dimensions and its scalarData
    const getDentascanSection = (
        x,
        lsf,
        dentascan_thickness,
        dicom_dimensions,
        dicomData,
        debug=false
        ) => {
        const one_layer_length = dicom_dimensions[0] * dicom_dimensions[1]

        //Get equation of normal at x
        const f_n = equationOfNormalPoly2(x, lsf)

        const pointA = [x - dentascan_thickness / 2, f_n(x - dentascan_thickness / 2)]
        const pointB = [x + dentascan_thickness / 2, f_n(x + dentascan_thickness / 2)]
        const angleRotation = calculateRotationAngle(pointA, pointB)

        //Rotated cartesian vector [1, 0] so that we get vector director of normal
        const x_rotation = rotateVector([1, 0], angleRotation)

        //Create normal array on incrementing from mix to max point with vector director
        const dentascan = []
        const min_point = findPointAtDistance(f_n, - dentascan_thickness / 2, [x, f_n(x)])
        const max_point = findPointAtDistance(f_n, dentascan_thickness / 2, [x, f_n(x)])
        // console.log(`${x}`, min_point, max_point)
        for(let layer_i = 0; layer_i < dicom_dimensions[2]; layer_i++){
            for (let i = min_point[0]; i < max_point[0]; i += x_rotation[0]) {
                const y = f_n(i)
                const index_in_layer = Math.floor(y) * dicom_dimensions[1] + Math.floor(i)
                const value_in_data = dicomData[index_in_layer + one_layer_length * layer_i]
                dentascan.push(value_in_data ? value_in_data : -1000)
            }
        }
        const dentascan_width = dentascan.length / (dicom_dimensions[2])

        //Compute long axial curve
        //Dentascan is thresholded to get only bone area
        const binarized_ds = dentascan.map((v, i, arr) => v > DENTASCAN_THRESHOLD_VALUE ? v : -1000)

        //Transposed so that w => h and h => w. This allows us to LSF with a curve represented by an order 5 equation
        const transposed_bin = transposeImage(binarized_ds, dentascan_width, dicom_dimensions[2])
        //minX and maxX where value is not -1000
        const {minX, maxX}  = findXRange(transposed_bin, dicom_dimensions[2], dentascan_width)

        //Get the indexes of teeth zone and keep only indexes that are teeth (!= -1000)
        const transposed_only_teeth_index = transposed_bin.map((v, i, arr) => v === -1000 ? null :  getCoordinatesFromIndex(i, dicom_dimensions[2], dentascan_width)).filter(v => v)

        //Curve fitting on a degree 5 polynomial
        const lsf_long_axial_curve = LSF_methods.polynomial(
            transposed_only_teeth_index,
            {order: 5, precision: 50}
        )

        const long_axial_curve_5 = layer => {
            return (lsf_long_axial_curve.equation[0] * layer ** 5 +
                lsf_long_axial_curve.equation[1] * layer ** 4 +
                lsf_long_axial_curve.equation[2] * layer ** 3 +
                lsf_long_axial_curve.equation[3] * layer ** 2 +
                lsf_long_axial_curve.equation[4] * layer +
                lsf_long_axial_curve.equation[5])
        }

        // I want to ensure that the polynomial does not diverge beyond the teeth zone.
        // The goal is to smooth out and converge the polynomial on the center outside the given interval while
        // preserving its original behavior within the interval [minX, maxX].
        // To achieve this, I use a modified function incorporating two sigmoid functions.
        const long_axial_liss = (minX, maxX) => {
            //a1 and a2 are smoothing parameters.
            const a1 = 0.1
            const a2 = 0.1
            //c0 is the convergeance on -inf, and c1 and +inf
            const c0 = dentascan_width / 2
            const c1 = dentascan_width / 2
            return (layer) => {
                const sigmoidLeft = 1 / (1 + Math.exp(-a1 * (layer - minX)));
                const sigmoidRight = 1 / (1 + Math.exp(a2 * (layer - maxX)));
                const original_long_axial_value = long_axial_curve_5(layer)
                return original_long_axial_value *
                    sigmoidLeft *
                    sigmoidRight +
                    c0 * (1 - sigmoidLeft) +
                    c1 * (1 - sigmoidRight)
            }
        }

        return {
            dentascan: dentascan,
            width: dentascan_width,
            normal: f_n,
            lsf: lsf_long_axial_curve,
            long_axial_curve: long_axial_liss(minX, maxX),
            interval: [min_point, max_point],
            director: x_rotation
        }
    }


    //Computation of panoramic base points, which are points in the dental curve
    //from where panoramic will be computed
    const generatePanoramicBasePoints = () => {
        // Delete measurements that are handmade (thus base points)
        // (Handmade is true because of an old implem where base_points were handmade, now they are not
        // but it's a way to identify them)
        const measurements = measurementService.getMeasurements()
            .filter(m => {return m.toolName === "ArrowAnnotate"})

        console.log('MEASU', measurements)

        measurements.forEach(pm => {
            if(pm.metadata.handmade && pm.displaySetInstanceUID === cornerstoneViewportService.getViewportInfoByIndex(0).viewportData.data.displaySetInstanceUID){
                commandsManager.runCommand('deleteMeasurement', {
                    uid: pm.uid
                })
            }
        })

        //Get volume from where we want to compute base points from
        const indexZeroDisplaySet = displaySetService.getDisplaySetByUID(
            cornerstoneViewportService.getViewportInfoByIndex(0).viewportData.data.displaySetInstanceUID
        )
        let baseId;
        let dicom_dimensions;
        let scalarData;
        let isContrastAlreadyGenerated;
        //If image is contrasted, volume we want to get infos from is not in viewport. We need to get infos from another way
        if(indexZeroDisplaySet?.SeriesInstanceUID.includes('contrasted')){
            isContrastAlreadyGenerated = true
            const baseVolumeSeriesUID = indexZeroDisplaySet.SeriesInstanceUID.replace('.contrasted', '')
            const baseVolumeSeries = DicomMetadataStore.getSeries(indexZeroDisplaySet.StudyInstanceUID, baseVolumeSeriesUID)
            baseId = baseVolumeSeries.instances[0].imageId
            const baseVolume = cache.getVolumeContainingImageId(baseId)
            scalarData = baseVolume.volume.scalarData
            dicom_dimensions = baseVolume.volume.dimensions
        }
        //Else image at index 0 is image we want to get infos from
        else{
            isContrastAlreadyGenerated = false
            const imageData = cornerstoneViewportService.getCornerstoneViewportByIndex(0).getImageData()
            baseId = cornerstoneViewportService.getCornerstoneViewportByIndex(0).getCurrentImageId()
            scalarData = imageData.scalarData
            dicom_dimensions = imageData.dimensions
        }
        const one_layer_length = dicom_dimensions[0] * dicom_dimensions[1]

        //Find minimal and maximal layer where one pixel is not -1000
        //This will allow us to mean layers with less -1000 pixels thus having a less dark result
        const minLayer = () => {
            for (let layer = 0; layer < dicom_dimensions[2]; layer++) {
                const startIndex = layer * one_layer_length;
                const endIndex = (layer + 1) * one_layer_length;
                const imageSlice = scalarData.slice(startIndex, endIndex);

                if (imageSlice.some(value => value > -1000)) {
                    return layer;
                }
            }
            return -1
        }
        const maxLayer = () => {
            for (let layer = dicom_dimensions[2] - 1; layer >= 0; layer--) {
                const startIndex = layer * one_layer_length;
                const endIndex = (layer - 1) * one_layer_length;
                const imageSlice = scalarData.slice(endIndex, startIndex);

                if (imageSlice.some(value => value > -1000)) {
                    return layer;
                }
            }
            return -1
        }
        const layerBounds = [minLayer(), maxLayer()]


        // The idea to compute mean is to first create an array of length one_layer_length where each index i of
        // the array is an array of length layer_length filled with the grey value of the pixel at layer i.
        const multi_layers_array = []
        for(let i = 0; i < one_layer_length; i++){
            //i is the index of the pixel in a layer
            let index = i + (one_layer_length * layerBounds[0])
            const index_i_array = []
            while (index < one_layer_length * layerBounds[1]){
                index_i_array.push(scalarData[index])
                index += one_layer_length
            }
            multi_layers_array.push(index_i_array)
        }
        // const max_array = multi_layers_array.map(pixel_i_all_layers => {return Math.max(...pixel_i_all_layers)})
        const mean_array = multi_layers_array.map(pixel_i_all_layers => {return pixel_i_all_layers.reduce((a, b) => a + b, 0) / pixel_i_all_layers.length})
        const min = Math.floor(mean_array.reduce((min, val) => (val < min ? val : min), mean_array[0]));
        const max = Math.floor(mean_array.reduce((max, val) => (val > max ? val : max), mean_array[0]));

        //Adjust contrast of image to enlight zone where control points should be placed
        const adjustContrast = (image, w, h, intensity) => {
            const adjustedImage = image.map(pixel => {
                const normalizedPixel = (pixel - min) / (max - min);
                const contrastAdjustedPixel = Math.pow(normalizedPixel, intensity);
                const clampedPixel = Math.min(1, Math.max(0, contrastAdjustedPixel));
                return Math.round(clampedPixel * (max - min) + min);
            });

            return adjustedImage;
        }
        const contrasted_mean = adjustContrast(mean_array, dicom_dimensions[0], dicom_dimensions[1], 4)

        // debugShowImage(mean_array, dicom_dimensions[0], dicom_dimensions[1])
        // debugShowImage(contrasted_mean, dicom_dimensions[0], dicom_dimensions[1])

        //Load and display contrasted_mean in Ohif since it's the image from where we can / will correct control points
        const voi_lut_module = metaData.get('voiLutModule', baseId)
        const image_pixel_module = metaData.get('imagePixelModule', baseId)
        const general_series_module = metaData.get('generalSeriesModule', baseId)
        const contrasted_metadata = {
            SeriesInstanceUID: general_series_module.seriesInstanceUID + '.contrasted',
            StudyInstanceUID: general_series_module.studyInstanceUID,
            BitsAllocated: image_pixel_module.bitsAllocated,
            BitsStored: image_pixel_module.bitsStored,
            Rows: dicom_dimensions[0],
            Columns: dicom_dimensions[1],
            WindowCenter: voi_lut_module.windowCenter[0],
            WindowWidth: voi_lut_module.windowWidth[0],
            PixelData: contrasted_mean
        }

        //If contrasted image is already generated (if we want to override already existing base points for example)
        //We don't need to load contrasted image in cache and in viewport, so we return an empty promise.
        const optionalLoadBlobInViewport = (metadata) => {
            if(isContrastAlreadyGenerated){
                return new Promise<void>((resolve) => resolve())
            }
            else{
                return loadBlobInViewport(metadata)
            }
        }

        optionalLoadBlobInViewport(contrasted_metadata).then(() => {
            // Refresh study list in hanging protocol module
            hangingProtocolService.refreshStudyList()
            const hpInfo = hangingProtocolService.getState();
            if (hpInfo.stageIndex < 1) {
                commandsManager.runCommand('toggleHangingProtocol', {
                    protocolId: 'panoramicViewport',
                    stageIndex: 1
                })
            }
            delay(500).then(() => {
                    //Update viewport since it has changed (from axial view to contrasted img)
                    const contrastedViewport = cornerstoneViewportService.getCornerstoneViewportByIndex(0)
                    const contrastedImageData = contrastedViewport.getImageData()

                    //K-means++ in contrasted image to isolate bone / jaw zone
                    const sk_means_result = skmeans(mean_array, 6)
                    const {centroids, idxs} = sk_means_result
                    const max_cluster_idx = centroids.indexOf(Math.max(...centroids))

                    //Binarize array between max cluster (highest values meaning bone pixels) and other
                    const binarized_array = mean_array.map((v, i, arr) => idxs[i] === max_cluster_idx ? v : -1000)
                    // debugShowImage(binarized_array, dicom_dimensions[0], dicom_dimensions[1])

                    //Dilate bone zone to eventually fill disconnections in dental zone
                    const dilate = (image, width, height, iterations) => {
                        const result = new Array(width * height).fill(-1000);

                        const getPixel = (x, y) => image[y * width + x] || 0;

                        for (let iteration = 0; iteration < iterations; iteration++) {
                            for (let y = 1; y < height - 1; y++) {
                                for (let x = 1; x < width - 1; x++) {
                                    const neighbors = [
                                        getPixel(x - 1, y - 1), getPixel(x, y - 1), getPixel(x + 1, y - 1),
                                        getPixel(x - 1, y), getPixel(x, y), getPixel(x + 1, y),
                                        getPixel(x - 1, y + 1), getPixel(x, y + 1), getPixel(x + 1, y + 1)
                                    ];

                                    result[y * width + x] = Math.max(...neighbors);
                                }
                            }

                            // Mettre  jour l'image pour la prochaine itration
                            for (let i = 0; i < width * height; i++) {
                                image[i] = result[i];
                            }
                        }

                        return result;
                    }
                    // const binarized_array_dilated = dilate(binarized_array, dicom_dimensions[0], dicom_dimensions[1], 5)
                    const binarized_array_dilated = binarized_array

                    //Get spatial clusters in binarized image (groups of pixels connected between them)
                    const spatialClustering = (image, width, height) => {
                        const clusters = [];
                        const visited = new Array(width * height).fill(false);

                        const getNeighbors = (index) => {
                            const neighbors = [];
                            const x = index % width;
                            const y = Math.floor(index / width);

                            for (let i = Math.max(0, x - 1); i <= Math.min(width - 1, x + 1); i++) {
                                for (let j = Math.max(0, y - 1); j <= Math.min(height - 1, y + 1); j++) {
                                    const neighborIndex = j * width + i;
                                    neighbors.push(neighborIndex);
                                }
                            }

                            return neighbors;
                        };

                        const exploreCluster = (startIndex, cluster) => {
                            const queue = [startIndex];

                            while (queue.length > 0) {
                                const currentIndex = queue.shift();

                                if (!visited[currentIndex] && image[currentIndex] !== -1000) {
                                    visited[currentIndex] = true;
                                    cluster.push(currentIndex);

                                    const neighbors = getNeighbors(currentIndex);

                                    for (const neighborIndex of neighbors) {
                                        if (!visited[neighborIndex] && image[neighborIndex] !== -1000) {
                                            queue.push(neighborIndex);
                                        }
                                    }
                                }
                            }
                        };

                        for (let i = 0; i < width * height; i++) {
                            if (!visited[i] && image[i] !== -1000) {
                                const newCluster = [];
                                exploreCluster(i, newCluster);
                                clusters.push(newCluster);
                            }
                        }

                        return clusters;
                    }
                    const spatial_clusters = spatialClustering(binarized_array_dilated, dicom_dimensions[0], dicom_dimensions[1])

                    //Indexes of biggest spatial cluster
                    const spatial_clusters_sorted = spatial_clusters.sort((sc1, sc2) => sc2.length - sc1.length)

                    //Find equation of polynomial going through dental zone with the use of the biggest spatial cluster
                    //corresponding to our dental zone
                    const findCurveFunction = () => {
                        let i = 0;
                        while (true){
                            let biggest_spatial_cluster_indexes = spatial_clusters_sorted[i]

                            //Indexes of biggest spatial cluster as X and Y values
                            let biggest_spatial_cluster_xy =  biggest_spatial_cluster_indexes
                                .map(v => getCoordinatesFromIndex(v, dicom_dimensions[0], dicom_dimensions[1]))

                            //Values of biggest spatial cluster
                            let biggest_spatial_cluster = binarized_array_dilated.map((v, i, arr) => {
                                return biggest_spatial_cluster_indexes.includes(i) ? v : -1000
                            })

                            //LSF method to get best fitting parabola along pixels in square
                            const lsf = LSF_methods.polynomial(biggest_spatial_cluster_xy, {order: 2, precision: 5})
                            console.log('LSF', lsf)

                            //If a <= 0, spatial cluster did not take right cluster and we go to the second bigest one
                            //to try it out
                            if(lsf.equation[0] > 0) {
                                const f = (x) => {return x*x*lsf.equation[0] + x*lsf.equation[1] + lsf.equation[2]}
                                return {
                                    f: f,
                                    lsf: lsf,
                                    biggest_spatial_cluster_xy: biggest_spatial_cluster_xy,
                                    biggest_spatial_cluster: biggest_spatial_cluster
                                }
                            }
                            else i += 1
                        }
                    }
                    const {
                        f,
                        lsf,
                        biggest_spatial_cluster_xy,
                        biggest_spatial_cluster
                    } = findCurveFunction()

                    //Find minimum and maximum of our binarized to determine where panoramic should start and where should end
                    //We want control points to start at right and left jaw, so we don't use biggest_cluster in this case
                    //since it corresponds to the dental zone, not jaws
                    let {minX, maxX} = findXRange(binarized_array_dilated, dicom_dimensions[0], dicom_dimensions[1])
                    // console.log('XMIN XMAX', minX, maxX)

                    // debugShowImage(binarized_array_dilated, dicom_dimensions[0], dicom_dimensions[1]).then(canvas => {
                    //     const ctx = canvas.getContext('2d')
                    //     const color = `rgb(255, 0, 0)`
                    //     for(const i of biggest_spatial_cluster_xy){
                    //         drawPoint(ctx, i.x, i.y, '', color, 1)
                    //     }
                    //     for(let i = 0; i < dicom_dimensions[0]; i++){
                    //         drawPoint(ctx, i, f(i), '', 'green', 2)
                    //     }
                    // })


                    //Compute control points : points that determine piecewise linear curve function through f
                    //They also be the points drawn in Viewport so that user can modify if the curve is not correct
                    const control_points = []
                    for(let i = minX; i <= maxX + 10; i += (maxX - minX) / (NUMBER_CONTROL_POINTS - 1)){
                        control_points.push([i, f(i)])
                    }

                    //Draw these base points in front on Viewport so that user can move them
                    //Transpose base_points from volume basis to viewport canvas basis
                    const controlPointsInWorld = control_points.map(cp => {
                        return contrastedImageData.imageData.indexToWorld([cp[0], cp[1], 0])
                    })
                    //Get ArrowAnnotate instance in order to create new interpolated annotation
                    const toolGroup = ToolGroupManager.getToolGroupForViewport(
                        contrastedViewport.id,
                        contrastedViewport.renderingEngineId
                    );
                    const arrowAnnotateInstance = toolGroup.getToolInstance("ArrowAnnotate")
                    console.log('Tool Group', toolGroup, arrowAnnotateInstance)
                    measurements.forEach(pm => {
                        if(pm.metadata.handmade && pm.displaySetInstanceUID === cornerstoneViewportService.getViewportInfoByIndex(0).viewportData.data.displaySetInstanceUID){
                            arrowAnnotateInstance.removeArrowAnnotation(pm.uid)
                        }
                    })
                    arrowAnnotateInstance.addNewAnnotationWithMovablePoints(contrastedViewport.element, controlPointsInWorld, 'rgb(0, 255, 0)')
            })
        })
    }


    //Base points that can be changed in front will be re-used to compute
    //A new dental curve thus a new panoramic
    const generatePanoramicFromBasePoints = (
        thickness,
        debug = false,
    ) => {
        //Get control points
        const controlPointsinViewport = measurementService.getMeasurements()
            .filter(m => {
                return  m.toolName === "ArrowAnnotate" &&
                        m.metadata.handmade &&
                        m.displaySetInstanceUID === cornerstoneViewportService.getViewportInfoByIndex(0).viewportData.data.displaySetInstanceUID
            })


        if(controlPointsinViewport.length === 0){
            uiNotificationService.show({
                title: `Panoramic Generation Error`,
                message: `Generate control points first before initiating the computation of the panoramic.`,
                type: 'error',
                duration: 3000,
            });
            return
        }

        //If user is asking to regenerate panoramic and there already is one for this serie, delete the previous one
        // Refresh study list in hanging protocol module
        hangingProtocolService.refreshStudyList()
        const hpInfo = hangingProtocolService.getState();
        //If stage is 2, we already have a panoramic
        if (hpInfo.stageIndex === 2) {
            const panoramicViewportInfo = cornerstoneViewportService.getViewportInfoByIndex(1)
            const dentascanViewportInfo = cornerstoneViewportService.getViewportInfoByIndex(2)
            console.info('Deleting old panoramic display set...', panoramicViewportInfo.viewportData.data.displaySetInstanceUID)
            console.info('Deleting old dentascan display set...', dentascanViewportInfo.viewportData.data.displaySetInstanceUID)
            displaySetService.deleteDisplaySet(panoramicViewportInfo.viewportData.data.displaySetInstanceUID)
            displaySetService.deleteDisplaySet(dentascanViewportInfo.viewportData.data.displaySetInstanceUID)
            commandsManager.runCommand('toggleHangingProtocol', {
                protocolId: 'panoramicViewport',
                stageIndex: 1
            })
        }

        //Display progress bar in front-end
        setIsProcessingPanoramic(true)

        //Get volume from where we want to compute panoramic on
        const contrastedDisplaySet = displaySetService.getDisplaySetByUID(
            cornerstoneViewportService.getViewportInfoByIndex(0).viewportData.data.displaySetInstanceUID
        )
        const baseVolumeSeriesUID = contrastedDisplaySet.SeriesInstanceUID.replace('.contrasted', '')
        const baseVolumeSeries = DicomMetadataStore.getSeries(contrastedDisplaySet.StudyInstanceUID, baseVolumeSeriesUID)
        const baseId = baseVolumeSeries.instances[0].imageId
        const baseVolume = cache.getVolumeContainingImageId(baseId)
        const scalarData = baseVolume.volume.scalarData
        const dicom_dimensions = baseVolume.volume.dimensions
        const one_layer_length = dicom_dimensions[0] * dicom_dimensions[1]

        //Get data from contrasted image
        const contrastedViewport = cornerstoneViewportService.getCornerstoneViewportByIndex(0)
        const contrastedData = contrastedViewport.getImageData()

        //Regenerate points from annoted points in viewport
        const controlPointsInVolume = controlPointsinViewport[0].points
            .map(point => {return contrastedData.imageData.worldToIndex(point)})
            .map(point => {return {x: point[0], y: point[1]}})
            .sort((p1, p2) => {return p1.x - p2.x})

        //LSF method to get best fitting parabola along base_points
        const lsf_dental_curve = LSF_methods.polynomial(controlPointsInVolume, {order: 2, precision: 5})
        const f = (x) => {return x*x*lsf_dental_curve.equation[0] + x*lsf_dental_curve.equation[1] + lsf_dental_curve.equation[2]}

        // For now we juste have control points
        // We need to regenerate base points, which are equitably distributed points between mix and max control points
        const base_points: BASE_POINT[] = []
        const minX = Math.min(...controlPointsInVolume.map(cp => cp.x))
        const maxX = Math.max(...controlPointsInVolume.map(cp => cp.x))

        for(let i = minX; i <= maxX + 10; i += (maxX - minX) / NUMBER_BASE_POINTS){
            const A = [i, f(i)]
            const i_incr = i + ((maxX - minX) / NUMBER_BASE_POINTS)
            if(i_incr > maxX + 10) break;
            const B = [i_incr, f(i_incr)]
            const angle = calculateRotationAngle(A, B)
            const vector_director = rotateVector([1, 0], angle)

            const base_point: BASE_POINT = {
                point_A: A,
                point_B: B,
                vector_director: vector_director,
                compute_long_axial_curve: true
            }
            base_points.push(base_point)
        }

        //TODO: DEBUG, Remove once not useful anymore
        if(debug) {
            const multi_layers_array = []
            for (let i = 0; i < one_layer_length; i++) {
                //i is the index of the pixel in a layer
                let index = i
                const index_i_array = []
                while (index < scalarData.length) {
                    index_i_array.push(scalarData[index])
                    index += one_layer_length
                }
                multi_layers_array.push(index_i_array)
            }
            const max_array = multi_layers_array.map(pixel_i_all_layers => {
                return Math.max(...pixel_i_all_layers)
            })

            let dentascans = []
            //Get dentascan sample to compute long axial curves
            for (const point of base_points) {
                dentascans.push(getDentascanSection(
                    point.point_A[0],
                    lsf_dental_curve,
                    thickness,
                    dicom_dimensions,
                    scalarData,
                    true
                ))
            }

            debugShowImage(max_array, dicom_dimensions[0], dicom_dimensions[1]).then(canvas => {
                const ctx = canvas.getContext('2d')
                //Draw sections taken i as example
                for (const point of base_points) {
                    //Draw normal to this control point
                    const i = point.point_A[0]
                    const f_n = equationOfNormalPoly2(i, lsf_dental_curve)
                    drawPoint(ctx, point.point_A[0], f(point.point_A[0]), '', 'red', 7)
                    drawPoint(ctx, point.point_B[0], f(point.point_B[0]), '', 'red', 7)
                    const min_point = findPointAtDistance(f_n, -80 / 2, [i, f_n(i)])
                    const max_point = findPointAtDistance(f_n, 80 / 2, [i, f_n(i)])
                    for (let j = min_point[0]; j < max_point[0]; j += 1) {
                        drawPoint(ctx, j, f_n(j), '', 'purple', 2)
                    }
                }

                for (let i = 0; i < dicom_dimensions[0]; i++) {
                    drawPoint(ctx, i, f(i), '', 'green', 2)
                }
            })


            for (const ds of dentascans) {
                debugShowImage(transposeImage(ds.dentascan.map((v, i, arr) => v > DENTASCAN_THRESHOLD_VALUE ? v : -1000), ds.width, dicom_dimensions[2]), ds.width, dicom_dimensions[2]).then(canvas => {
                    const ctx = canvas.getContext('2d')
                    for (let i = 0; i < dicom_dimensions[2]; i++) {
                        for(let i = 0; i < dicom_dimensions[2]; i += 20){
                            const n_thickness = 30
                            const f_n = equationOfNormal(i, ds.long_axial_curve)
                            const pointA = [i - n_thickness / 2, f_n(i - n_thickness / 2)]
                            const pointB = [i + n_thickness / 2, f_n(i + n_thickness / 2)]
                            const angleRotation = calculateRotationAngle(pointA, pointB)

                            //Rotated cartesian vector [1, 0] so that we get vector director of normal
                            const x_rotation = rotateVector([1, 0], angleRotation)

                            const min_point = findPointAtDistance(f_n, - n_thickness / 2, [i, f_n(i)])
                            const max_point = findPointAtDistance(f_n, n_thickness / 2, [i, f_n(i)])
                            for (let i = min_point[0]; i < max_point[0]; i += x_rotation[0]) {
                                const y = f_n(i)
                                drawPoint(ctx, i, y, '', 'purple', 2)
                            }

                        }
                        drawPoint(ctx, i, ds.long_axial_curve(i), '', 'red', 2)
                    }
                })
            }
        }

        //Iteration on normals
        //First determine width of panoramic
        let panoramic_width = 0
        base_points.forEach(p => {for (let i = p.point_A[0]; i < p.point_B[0]; i += p.vector_director[0]) panoramic_width += 1})
        console.log('PANORAMIC WIDTH', panoramic_width)
        //Init panoramic array
        let panoramic_normals_iterative = new Array(panoramic_width * dicom_dimensions[2]).fill(-1000)
        //Init dentascans array
        let dentascans_array = new Array(panoramic_width).fill(null)

        //_______________________________________________________________________________________________//

        //Return an array of offsets
        //On each index representing a base_points, the value represents the width value on the panoramic
        //from where the corresponding base_point begins.
        const buildOffsets = (base_points) => {
            const offsets = []
            base_points.forEach((p, n) => {
                let offset = 0
                for(let i = p.point_A[0]; i < p.point_B[0]; i += p.vector_director[0]){ offset += 1 }
                offsets.push(offset + (n > 0 ? offsets[n - 1] : 0))
            })
            offsets.unshift(0)
            return offsets
        }

        const offsets = buildOffsets(base_points)
        console.log('Js', offsets)

        //This promise take a base point and an offset
        //It fills panoramic_array from base_point to next base_point
        //Offset as explained earlier is the starting width of the base point
        const buildPanoramicPromise = (base_point, offset) => {
            return new Promise<void>((resolve, reject) => {
                //From start of base point to start of other base point, incremented by vector dirtector between those two points
                for(let i = base_point.point_A[0]; i < base_point.point_B[0]; i += base_point.vector_director[0]){
                    //Compute dentascan at this x
                    const dentascan = getDentascanSection(
                        i,
                        lsf_dental_curve,
                        thickness,
                        dicom_dimensions,
                        scalarData
                    )
                    dentascans_array[offset] = dentascan.dentascan
                    //For each layer
                    for(let layer = 0; layer < dicom_dimensions[2]; layer++){
                        // Compute normal equation of long_axial_curve at layer
                        const long_axial_normal = equationOfNormal(layer, dentascan.long_axial_curve)
                        const normal_vector_director = rotateVector([1, 0],
                            calculateRotationAngle(
                                [layer - NORMAL_LONG_AXIAL_CURVE_THICKNESS / 2, long_axial_normal(layer - NORMAL_LONG_AXIAL_CURVE_THICKNESS / 2)],
                                [layer + NORMAL_LONG_AXIAL_CURVE_THICKNESS / 2, long_axial_normal(layer + NORMAL_LONG_AXIAL_CURVE_THICKNESS / 2)]
                            )
                        )
                        //Get min and max point along normal of long_axial_curve which length between the two is NORMAL_LONG_AXIAL_CURVE_THICKNESS
                        const min_point = findPointAtDistance(long_axial_normal, - NORMAL_LONG_AXIAL_CURVE_THICKNESS / 2, [layer, long_axial_normal(layer)])
                        const max_point = findPointAtDistance(long_axial_normal, NORMAL_LONG_AXIAL_CURVE_THICKNESS / 2, [layer, long_axial_normal(layer)])
                        const values_along_normal = []

                        //Get values of point along normal between min and max
                        for (let k = min_point[0]; k < max_point[0]; k += normal_vector_director[0]) {
                            const v = long_axial_normal(k)
                            const point_coordinates_at_layer = [
                                dentascan.interval[0][0] + v * dentascan.director[0],
                                dentascan.interval[0][1] + v * dentascan.director[1],
                            ]
                            const x_at_point = Math.floor(point_coordinates_at_layer[0])
                            const y_at_point = Math.floor(point_coordinates_at_layer[1])
                            const value_on_scalardata = scalarData[one_layer_length * layer + y_at_point * dicom_dimensions[0] + x_at_point]
                            values_along_normal.push(value_on_scalardata)
                        }

                        //Add mean of all values as value in panoramic result
                        const mean = values_along_normal.reduce((a, b) => a + b, 0) / values_along_normal.length;
                        panoramic_normals_iterative[panoramic_width * (dicom_dimensions[2] - layer) + offset] = mean
                    }
                    offset += 1
                }
                resolve()
            })
        }

        //Array of promises for each base_point
        const buildPanoramicPromisesArray = []
        base_points.forEach((b, n) => {
            buildPanoramicPromisesArray.push(() => {
                return buildPanoramicPromise(b, offsets[n])
            })
        })

        //Array at start is filled with false
        //When a promise at a specific base_point is done, its index goes to true
        //This array allows us to track state of the promises to display progress bar in front
        const doneArray = new Array(base_points.length).fill(false)

        //Recursive function to execute promises in array
        //When promise at index x is done, update doneArray by putting true value and wait a bit
        //to call recursively function of x + 1 index.
        const executePromises = (promises, index = 0) => {
            if (index < promises.length) {
                return promises[index]().then(() => {
                    doneArray[index] = true
                    delay(100).then(() => {
                        executePromises(promises, index + 1)
                    })
                });
            } else {
                return Promise.resolve();
            }
        }

        //Update progress value in front-end by getting each 250ms the number of true values in doneArray
        //THIS WORKS ONLY WHEN PARENT FUNCTION IS CALLED WITH debug = false
        const waitUntilArrayDone = () => {
            return new Promise<void>((resolve, reject) => {
                (function waitForArray(){
                    const completed = doneArray.filter(el => {return el}).length
                    setProcessingPourcent(~~((completed * 100) / base_points.length))
                    if(completed === base_points.length){
                        return resolve();
                    }
                    else {
                        setTimeout(waitForArray, 50);
                    }
                })()
            })
        }

        //Wait until panoramic is completed
        waitUntilArrayDone().then(() => {
            console.log('DONE')
            const sharpened_panoramic = sharpen(panoramic_normals_iterative, panoramic_width, dicom_dimensions[2])

            if(debug) debugShowImage(sharpened_panoramic, dicom_dimensions[2], panoramic_width)

            //Display panoramic in OHIF Viewport
            const voi_lut_module = metaData.get('voiLutModule', baseId)
            const image_pixel_module = metaData.get('imagePixelModule', baseId)
            const general_series_module = metaData.get('generalSeriesModule', baseId)

            if(!cornerstoneViewportService.getCornerstoneViewportByIndex(0)){
                uiNotificationService.show({
                    title: `Panoramic Generation Error`,
                    message: `No viewport detected.`,
                    type: 'error',
                    duration: 3000,
                });
                return
            }
            const panoramic_metadata = {
                SeriesInstanceUID: general_series_module.seriesInstanceUID + '.panoramic',
                StudyInstanceUID: general_series_module.studyInstanceUID,
                BitsAllocated: image_pixel_module.bitsAllocated,
                BitsStored: image_pixel_module.bitsStored,
                Rows: dicom_dimensions[2],
                Columns: panoramic_width,
                WindowCenter: voi_lut_module.windowCenter[0],
                WindowWidth: voi_lut_module.windowWidth[0],
                PixelData: sharpened_panoramic
            }
            loadBlobInViewport(panoramic_metadata).then(panoramic_viewportdata => {
                const dentascan_object = {
                    SeriesInstanceUID: general_series_module.seriesInstanceUID + '.dentascan',
                    StudyInstanceUID: general_series_module.studyInstanceUID,
                    BitsAllocated: image_pixel_module.bitsAllocated,
                    BitsStored: image_pixel_module.bitsStored,
                    Rows: dicom_dimensions[2],
                    Columns: -1,
                    WindowCenter: voi_lut_module.windowCenter[0],
                    WindowWidth: voi_lut_module.windowWidth[0],
                    PixelData: dentascans_array
                }

                loadMultipleBlobsInViewport(dentascan_object).then(dentascans_viewportdata => {
                    // Refresh study list in hanging protocol module
                    hangingProtocolService.refreshStudyList()
                    const hpInfo = hangingProtocolService.getState();
                    commandsManager.runCommand('toggleHangingProtocol', {
                            protocolId: 'panoramicViewport',
                            stageIndex: 2
                    })
                    setPanoramicMetadada({
                        ...panoramicMetadada,
                        update: !panoramicMetadada.update,
                        dentascanViewportData: dentascans_viewportdata,
                        panoramicViewportData: panoramic_viewportdata,
                        panoramic: {
                            width: panoramic_width,
                            height: dicom_dimensions[2]
                        }
                    })
                    //Save state in cache to be retrieved if we reopen panoramic panel
                    cornerstoneCacheService.setPanoramicStateCache({
                        ...panoramicMetadada,
                        update: !panoramicMetadada.update,
                        dentascanViewportData: dentascans_viewportdata,
                        panoramicViewportData: panoramic_viewportdata,
                        panoramic: {
                            width: panoramic_width,
                            height: dicom_dimensions[2]
                        }
                    }, general_series_module.seriesInstanceUID)

                    console.log('DISPLAY SETS', displaySetService.getDisplaySetCache())
                })
            })
        })

        //Launch panoramic computing
        executePromises(buildPanoramicPromisesArray)
    }

    // @ts-ignore
    return (
        <div style={{width:'100%', height:'100%'}}>
            <div style={{width:'100%', display:'flex', flexDirection:'column', height:'100%'}}>
                <p style={{color:'white', fontWeight:'bold', marginBottom:'20px'}}>{'Panoramic Generation'}</p>
                <p style={{color:'white', fontSize:'small', marginBottom:'10px'}}>{'1 - Generate control points from where panoramic will be computed with'}</p>
                <Button onClick={generatePanoramicBasePoints} style={{marginBottom:'20px'}}>{"Generate Control Points"}</Button>
                <p style={{color:'white', fontSize:'small', marginBottom:'20px'}}>{'2 - Adjust the points as needed so that the green line fits best around the white area of the image (along the dental curve). The points do not need to be evenly distributed. You can change brightness and contrast of the image to enlighten the white line with the Window Level tool in toolbar.'}</p>
                <div style={{width:'90%', display:'flex', flexDirection:'column', marginBottom:'20px'}}>
                    <p style={{color:'white', fontSize:'small', marginBottom:'10px'}}>{'3 - Once the points are well adjusted, you can modify some parameters but be careful : the lower the parameters are set, the faster the execution, but the quality of the panoramic view may be compromised.'}</p>
                    <p style={{color:'#5ACCE6', fontSize:'smaller', marginBottom:'5px'}}>{'Steps of computation :'}</p>
                    <div style={{width:"100%", display:'flex'}}>
                        <input
                            type="range"
                            id="d_section"
                            name="d_section"
                            value={`${~~(numberBasePoints)}`}
                            min="5" max={"45"}
                            style={{width:'70%', marginRight:'5%'}}
                            onChange={(e) => {setNumberBasePoints(parseInt(e.target.value))}}
                        />
                        <p style={{color:'#5ACCE6', fontSize:'small', width:'25%'}}>{`${numberBasePoints}`}</p>
                    </div>
                </div>
                <p style={{color:'white', fontSize:'small', marginBottom:'10px'}}>{'4 - Once the chosen parameters are good, you can initiate the computation'}</p>
                <Button onClick={() => {generatePanoramicFromBasePoints(THICKNESS, false)}} style={{marginBottom:'10px'}}>{"Compute Panoramic"}</Button>
                {
                    isProcessingPanoramic &&
                    <div className={'w-full mt-[10px] mb-[20px]'}>
                        <p style={{color:'white', fontSize:'small'}}>{'Creating panoramic...'}</p>
                        <ProgressBar
                            className={'w-full mt-[3px]'}
                            bgColor={processingPourcent < 100 ? '#5ACCE6' : '#50C878'}
                            completed={processingPourcent}
                            transitionDuration={"0.05s"}
                            transitionTimingFunction={"linear"}
                        />
                    </div>
                }
                { panoramicMetadada.panoramic?.width &&
                    <div style={{width:'100%', display:'flex', flexDirection:'column', justifyContent:'space-evenly', height:'18%'}}>
                        <p style={{color:'white', fontWeight:'bold'}}>{'Dentascan Editing'}</p>
                        <p style={{color:'white', fontSize:'small', marginBottom:'10px'}}>{'5 - Scroll through the dentascans using this slider. The central dentascan is represented by the white line on the panoramic view.'}</p>
                        <div style={{width:'90%', display:'flex', height:'10%', flexDirection:'row', justifyContent:'space-between'}}>
                            <p style={{color:'white', fontSize:'smaller'}}>{'Section :'}</p>
                            <input
                                type="range"
                                id="d_section"
                                name="d_section"
                                value={`${~~(panoramicMetadada.dentascanViewportData.data.initialImageIndex)}`}
                                min="0" max={`${panoramicMetadada.panoramic.width - 1}`}
                                style={{width:'70%'}}
                                onChange={(e) => {setPanoramicMetadada({
                                    ...panoramicMetadada,
                                    update: !panoramicMetadada.update,
                                    dentascanViewportData: {
                                        ...panoramicMetadada.dentascanViewportData,
                                        data: {
                                            ...panoramicMetadada.dentascanViewportData.data,
                                            initialImageIndex: ~~(e.target.value)
                                        }
                                    }
                                })}}
                            />
                        </div>
                    </div>
                }
            </div>
        </div>
);
}